import React from 'react';
import { Switch,InputNumber,Modal } from 'antd';
import axios from "axios";
import { preURL } from "../../axios/config";
import { withCookies } from "react-cookie";
import "./Setting.less";

class Setting extends React.Component {
  state = {
    setting: 0,
  }
  componentWillMount(){
    const user = this.props.cookies.get('user');
    this.setState({
      email: user.email,
      setting: user.setting,
      check4: user.setting < 2,
      check6: user.setting % 2 === 0,
    });
    axios({
      method: "post",
      url: preURL + "/api/recite/cet4",
      dataType: "json",
      data: {
        user: user.email
      },
      headers: {
        "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
      }
    }).then(response => {
      console.log("api/recite/cet4 response:", response);
      const data = response.data.data;
      this.setState({
        counter: data.counter, //
      });
    }).catch(error => {
      console.log("api/recite/cet4 error:", error);
    });
  }
  onChange4 = item =>{
    if (!this.state.check6 && !item) {
      Modal.error({title: '', content: ''});
      return;
    }
    this.setState({
      check4: item
    });
    let _set = 0;
    if (this.state.check6 && item){
      _set = 0;
    } else if (this.state.check6 && !item){
      _set = 2;
    } else if (!this.state.check6 && item){
      _set = 1;
    }
    axios({
      method: "post",
      url: preURL + "/api/setting/plan",
      dataType: "json",
      data: {
        user: this.state.email,
        counter: this.state.counter,
        setting: _set,
      },
      headers: {
        "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
      }
    }).then(response => {
      console.log("setting/plan response:", response);
      if (response.data.code === "200"){
        this.setState({
          check4: item
        });
      }
    }).catch(error => console.error("setting/plan error:", error));
  }
  onChange6 = item =>{
    if (!this.state.check4 && !item) {
      Modal.error({title: '', content: ''});
      return;
    }
    let _set = 0;
    if (this.state.check4 && item){
      _set = 0;
    } else if (this.state.check4 && !item){
      _set = 1;
    } else if (!this.state.check4 && item){
      _set = 2;
    }
    axios({
      method: "post",
      url: preURL + "/api/setting/plan",
      dataType: "json",
      data: {
        user: this.state.email,
        counter: this.state.counter,
        setting: _set,
      },
      headers: {
        "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
      }
    }).then(response => {
      console.log("setting/plan response:", response);
      if (response.data.code === "200"){
        this.setState({
          check6: item
        });
      }
    }).catch(error => console.error("setting/plan error:", error));
  }
  onChange = item => {
    let _set = 0;
    if (this.state.check4 && this.state.check6){
      _set = 0;
    } else if (this.state.check4 && !this.state.check6){
      _set = 1;
    } else if (!this.state.check4 && this.state.check6){
      _set = 2;
    }
    axios({
      method: "post",
      url: preURL + "/api/setting/plan",
      dataType: "json",
      data: {
        user: this.state.email,
        counter: item,
        setting: _set,
      },
      headers: {
        "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
      }
    }).then(response => {
      console.log("setting/plan response:", response);
    }).catch(error => console.error("setting/plan error:", error));
  }
  render(){
    const { check4,check6 } = this.state;
    return (
    <div className='setting'>
      <div className='plan'><span></span>{this.state.counter?<InputNumber size="large" min={1} defaultValue={this.state.counter} onChange={this.onChange} />:null}</div>
      <div className='cet4'><span></span><Switch checkedChildren="" unCheckedChildren="" checked={check4} onChange={this.onChange4} /></div>
      <div className='cet6'><span></span><Switch checkedChildren="" unCheckedChildren="" checked={check6} onChange={this.onChange6} /></div>
    </div>);
  }
}

export default withCookies(Setting);


